import React, { useEffect, useState } from 'react';
import Cookies from 'js-cookie';
import { useNavigate, NavLink } from 'react-router-dom';
import axios from 'axios';
import styleess from './cssemp/employee.module.css';
import img from '../image/icon.png';
import Button from 'react-bootstrap/Button';

function Employee() {
  const [data, setData] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    fetch('http://localhost:8083/services')
      .then(res => res.json())
      .then(data => {
        if (Array.isArray(data)) {
          setData(data);
        } else {
          console.error('Expected data to be an array, but got:', data);
        }
      })
      .catch(err => console.log(err));
  }, []); // Add an empty dependency array to run this effect only once

  const groupDataByUsername = (data) => {
    if (!Array.isArray(data)) {
      console.error('Expected data to be an array, but got:', data);
      return {};
    }
    
    const groupedData = {};
    data.forEach(item => {
      if (!groupedData[item.namedoctor]) {
        groupedData[item.namedoctor] = [item];
      } else {
        groupedData[item.namedoctor].push(item);
      }
    });
    return groupedData;
  };

  const groupedData = groupDataByUsername(data);

  // Uncomment and use this if you want to handle the login check
  // useEffect(() => {
  //   const login = Cookies.get('loginemp');
  //   if (login !== 'true') {
  //     navigate('/signinEmp');
  //   }
  // }, [navigate]);

  const handleRemove = async (id) => {
    try {
      await axios.delete(`http://localhost:8083/removetest/${id}`);
      window.location.reload();
    } catch (err) {
      console.error("Error deleting record:", err);
    }
  };

  return (
    <>
      <div className={styleess.nav}>
        <img src={img} alt="" />
        <h5>Technical Support</h5>
      </div>
      <div className={styleess.bodyenplo}>
        <div className={styleess.tablee}>
          <h1 className={styleess.h1}>
          Technical Support
            <h1 > IT</h1>
          </h1>
          <table>
            <thead>
              <tr>
                <th>id</th>
                <th>Name Doctor</th>
                <th>Devices</th>
                <th>Name Devices</th>
                <th>Reports</th>
                <th>Delete</th>

              </tr>
            </thead>
            <tbody>
              {Object.keys(groupedData).map(namedoctor => (
                <React.Fragment key={namedoctor}>
                  <tr>
                    <td colSpan="7"></td>
                  </tr>
                  {groupedData[namedoctor].map((item, i) => (
                    <tr key={i}>
                      {/* <td></td> */}
                      <td>{item.id}</td>
                    <td>{namedoctor}</td>
                      <td>{item.nameimages}</td>
                      <td><img src={item.images} alt="" className={styleess.image} /></td>
                      <td>{item.textarea}</td>
                      {/* variant="danger" */}
                      <td><Button  onClick={() => handleRemove(item.id)}>Delete</Button></td>
                    </tr>
                  ))}
                </React.Fragment>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}

export default Employee;
